body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  height: 7vh;
  background-color: #4d4d4d;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.nav .home-button {
  font-size: 2em;
  color: white;
  text-decoration: underline;
  margin-left: 0.5em;
  margin-top: 0.2em;
}

.issue-app-container {
  display: flex;
  flex-direction: row;
}

.nav .url-input-form input {
  margin-right: 1em;
}

.nav .url-input-form {
  -webkit-transform: translateY(25%);
          transform: translateY(25%);
  margin-right: 1em;
}
.results-selector {
  width: 38%;
  height: 93vh;
  background-color: #f4f4f4;
  border-right: 1px solid rgb(206, 206, 206);
  overflow: scroll;
}

.issue-title-card {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0.5em 0 0.5em 0;
  border-bottom: 1px solid rgb(206, 206, 206);
}

.issue-title-card p {
  color: rgb(69, 69, 69);
  margin: 0 0 0 0.5em;
}
h1 {
  color: #4d4d4d;
}

.labels-container {
  display: flex;
  flex-direction: row;
}

.label-badge {
  border-radius: 4px;
  padding: 0;
  margin-right: 1em;
}

.label-badge p {
  margin: 0.5em;
  color: rgb(244, 244, 244);
}

.issue-body {
  padding: 0.5em;
  margin-right: 1em;
  margin-bottom: 1em;
  border: 1px solid #4d4d4d;
  border-radius: 5px;
}

.issue-body > span > p > code {
  color: rgb(184, 68, 87);
  background: rgb(210, 210, 210);
}

.issue-body > span > pre {
  padding: 0.3em;
  margin: 0 1em 0 1em;
  background: rgb(215, 227, 232);
}

.issue-body > span > pre > code {
  color: rgb(45, 65, 75);
}
.issue-viewer-container {
  margin-left: 1em;
  height: 93vh;
  overflow: scroll;
  width: 62%;
}
